.hero {
  background-image: linear-gradient(rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.6)),
    url('../black-images/pexels-life-of-pix-316902.jpg');
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;

  height: 100vh;

  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 2rem;
  color: white;
}
.hero h1 {
  color: #ada6c4;
  font-size: 2rem;
}

.hero p {
  color: gray;
}

.hero img {
  height: 100px;
}

