.quote-container {
  width: 500px;
  max-width: 900px;
  padding: 10px;

  border-radius: 10px;
  background-color: rgba(255, 255, 255, 0.4);
  box-shadow: 1px 10px 10px 10px rgba(0, 0, 0, 0.2);
}

.quote-text {
  font-size: 1rem;
}
.long-quote {
  font-size: 0.8rem;
}
.fa-quote-left {
  font-size: 4rem;
}

.button-container {
  margin-top: 15px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.button {
  cursor: pointer;
  font-size: 1rem;
  border: none;
  background-color: #333;
  color: #fff;
  border-radius: 10px;
  padding: 0.5rem 1.8rem;
  /* box-shadow:0 0.3rem  rgba(121, 121, 121,0.65 ) */
}
.button:hover {
  filter: brightness(110%);
}

.twitter-button:hover {
  color: #38a1f3;
}
.quote-author {
  margin-top: 15px;
  font-size: 1rem;
  font-style: italic;
  font-weight: bold;
  color: #dbf0ff;
}

/* media queries */
@media screen and (max-width: 640px) {
  .hero {
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  .hero h1 {
    color: #ada6c4;
    font-size: 1.6rem;
  }
  .hero img {
    height: 42px;
  }
  .img-container {
    display: flex;
    margin-bottom: 1rem;
    /* background-color: #38a1f3; */
  }
  .quote-container {
    width: auto;
  }
}
